.dropzone .drop-area{min-height:150px;display:flex;flex-direction:column;align-items:center;justify-content:center;background-color:#fafafa;border:2px dashed #9b9ca7;border-radius:15px;text-align:center}.dropzone .dropzone-list{margin-top:16px;display:flex;flex-direction:row;gap:24px}.dropzone .dropzone-list .single-dropzone-file{background-color:#e7faff;border-radius:16px;display:flex;flex-direction:row;padding:4px 15px 4px 4px;cursor:pointer}.dropzone .dropzone-list .single-dropzone-file .message-icon{border-radius:16px;background-color:#a0d7e7;padding:15px;margin-right:16px}.dropzone .dropzone-list .single-dropzone-file .label-space{display:flex;flex-direction:column;justify-content:center}.dropzone .dropzone-list .single-dropzone-file .label-space .label{color:#5f75ee;font-weight:600;font-size:13px}.dropzone .dropzone-list .single-dropzone-file .label-space .file-dimension{color:#11142d;font-weight:600;font-size:13px}.dropzone .dropzone-list .single-dropzone-file .delete-space{display:flex;align-items:center;justify-content:center}.dropzone .dropzone-list .single-dropzone-file .delete-space button{min-width:0;min-height:0;padding:2px;margin-left:12px}.dropzone .dropzone-list .single-dropzone-file .delete-space button svg{max-width:36px;max-height:36px;min-width:36px;min-height:36px}