.subscriptionCard{position:relative;background-color:#f7f9fc;padding:50px 40px;min-width:300px;max-width:350px;border-radius:8px;box-shadow:0 4px 40px rgba(73,87,101,.12);display:flex;flex-direction:column}.subscriptionCard .description{margin-bottom:10px}.subscriptionCard h2.title{padding:0;margin:0;color:#000;font-size:18px;text-align:left;text-transform:capitalize}.subscriptionCard .badge{position:absolute;top:26px;right:0;font-size:10px;font-weight:bold;text-transform:uppercase;color:#fff;padding:4px 10px;border-radius:0;background-color:var(--primary-color)}.subscriptionCard .titleFeatures{margin-top:43px;color:#000;font-weight:700;font-size:16px;text-align:left}.subscriptionCard .price{font-size:25px;padding-top:15px;padding-bottom:40px;text-align:center}.subscriptionCard .priceValue{font-weight:bold;font-size:40px}.subscriptionCard .paymentFrequency{font-size:18px;margin-left:5px}.subscriptionCard .productPrice{background:var(--primary-color);border:2px solid var(--primary-color);color:var(--secondary-color);min-height:50px;text-align:center;display:inline-block;font-size:18px;font-weight:200;transition:all .3s;width:100%;font-weight:bold;box-shadow:7px 7px 6px -3px #16a69933}.subscriptionCard .productPrice:hover{background:var(--primary-color);transition:all .3s;transform:translateY(-10px);transform:scale(1.1);z-index:4}.subscriptionCard .product-price b{margin-left:5px}.subscriptionCard .listFeatures{justify-content:center;padding-left:15px;margin-bottom:0;display:flex;flex-direction:column}.subscriptionCard .singleFeature{display:flex;flex-direction:row;align-items:center}.subscriptionCard .singleFeature > svg{margin-right:13px}.subscriptionCard .singleFeature:not(:last-of-type){margin-bottom:7px}.subscriptionCard .singleFeature li{font-size:16px;list-style:none}.subscriptionCard.mostConvenient{background-color:#fff;transform:translateY(-20px);padding-top:70px}.subscriptionCard.mostConvenient .priceValue{color:var(--primary-color)}@media only screen and (max-width: 768px){.subscriptionCard.mostConvenient{transform:none}}