.contactCard{position:relative;padding:var(--standard-padding);min-width:300px;border-radius:var(--standard-border-radius);box-shadow:0 4px 40px rgba(73,87,101,.12);display:flex;flex-direction:column;justify-content:center;text-align:center;align-items:stretch}.contactCard .contactFields{gap:var(--standard-fields-gap);display:flex;flex-direction:column;align-items:stretch}.contactCard .btnContainer{display:flex;align-self:center;justify-content:center;align-items:center;text-align:center;margin-top:var(--standard-fields-gap)}.contactCard .btnContainer .contactBtn{text-transform:uppercase;transition:all .5s ease-in;padding-top:.5em;padding-bottom:.5em}.contactCard .btnContainer .contactBtn:hover{background-color:#16a699;color:#fff;font-weight:bold;transition:all .5s ease-in;transform:scale(1.1)}.contactCard .contactSingleField label{color:#16a699}.contactCard .contactSingleField .MuiFormControl-root{width:325px}