.loginCard{position:relative;background-color:#f7f9fc;padding:50px 40px;min-width:300px;max-width:350px;border-radius:8px;box-shadow:0 4px 40px rgba(73,87,101,.12);display:flex;flex-direction:column;height:354px;justify-content:center;text-align:center;align-items:center}.loginCard .loginFields{margin-top:2em;gap:1.8em;display:flex;flex-direction:column;margin-bottom:.6em}.loginCard .forgotPwd{display:flex;align-self:flex-end}.loginCard .btnContainer{display:flex;align-self:center;justify-content:center;align-items:center;text-align:center}.loginCard .loginBtn{background-color:#16a699;color:#fff;margin-top:2em;width:100%;min-width:345px;text-transform:uppercase;transition:all .5s ease-in;margin-bottom:1.8em;padding-top:.5em;padding-bottom:.5em}.loginCard .loginBtn:hover{background-color:#16a699;color:#fff;font-weight:bold;transition:all .5s ease-in;transform:scale(1.1)}.loginCard .loginSingleField{color:#000;width:351px}.loginCard .loginSingleField label{color:#16a699}.loginCard .loginSingleField fieldset.MuiOutlinedInput-notchedOutline{border-color:#16a699 !important}.loginCard .loginSingleField:hover{color:#000;width:351px}label .loginCard .loginSingleField:hover:hover{color:#16a699}.loginCard .loginSingleField:hover .MuiOutlinedInput-notchedOutline:hover{border-color:#16a699}.loginCard a{color:#000}.loginCard a:visited{color:#000}.loginCard .MuiInputBase-root{color:#000}.loginCard .socialLogin{width:100%}.loginCard .socialLoginBtns{margin-top:1em;margin-bottom:2em;display:flex;gap:1.5em;justify-content:center;align-items:center;text-align:center;align-self:center}.loginCard .roundButton{background-color:#fff;border-radius:999999px;min-height:64px;min-width:64px;transition:all .5s ease-in}.loginCard .roundButton:hover{transition:all .5s ease-in;transform:scale(1.2)}.loginCard a.registerLink{text-decoration:underline;color:#16a699;font-weight:bold}