.card > figure{margin:0}.card__img-wrapper{overflow:hidden;position:relative;width:100%}.card__img{border-style:none;height:291px;-webkit-transition:ease all .9s;-moz-transition:ease all .9s;-o-transition:ease all .9s;-webkit-transform:scale(1);-moz-transform:scale(1);-o-transform:scale(1);transform:scale(1);transition:ease all .9s;vertical-align:middle;width:auto}.card__img-animation{-webkit-transform:scale(1.2) rotate(0.1deg);-moz-transform:scale(1.2) rotate(0.1deg);-ms-transform:scale(1.2) rotate(0.1deg);-o-transform:scale(1.2) rotate(0.1deg);transform:scale(1.2) rotate(0.1deg)}.card__img-overlay{background-color:#000000b3;bottom:0;height:100%;left:0;opacity:0;position:absolute;right:0;top:0;transition:opacity 400ms;width:100%}.card__img-overlay:hover{opacity:1}.card__img-overlay--on{opacity:1}.card__img-overlay-button{box-sizing:border-box;margin-top:40%;text-align:center;-webkit-transform:translateY(-50%);transform:translateY(-50%)}.card__img-overlay-button-link{background-color:transparent;border:1px solid var(--secondary-color);border-radius:0;color:var(--secondary-color);display:inline-block;line-height:1;margin:2;padding:10px 23px;transition:all 400ms;text-decoration:none}.card__img-overlay-button-link:hover{background-color:var(--primary-color);border:1px solid var(--primary-color)}.card__info{max-width:100%;padding:20px 25px}.card__info-title{font-size:24px;margin:0;padding:0}.card__info-title > a{-webkit-box-orient:vertical;-webkit-line-clamp:1;color:var(--primary-color);display:-webkit-box;text-decoration:none;overflow:hidden;text-overflow:ellipsis}.card__info-excerpt{-webkit-box-orient:vertical;-webkit-line-clamp:2;display:-webkit-box;margin-top:5px;overflow:hidden;text-overflow:ellipsis}