.datePicker{display:flex;flex-direction:column}.datePicker > label{color:#000;font-weight:600;margin-bottom:10px}.datePicker fieldset{border:2px solid var(--primary-color);border-radius:8px}.datePicker svg{fill:var(--primary-color)}.datePicker:hover fieldset{border-color:var(--secondary-color) !important}.datePicker:hover svg{fill:var(--secondary-color)}